import { mapGetters } from 'vuex'
import SignBox from './components/SignBox'
import Products from './components/Products'
import Skeleton from '@/components/Skelenton'

import { parseTime } from '@/utils/time'
import { cloudFunction } from '@/utils/request'

let recordId = null
let ownRecord = null

export default {
  components: {
    SignBox,
    Skeleton,
    Products
  },

  data () {
    return {
      loading: false,
      signLoading: false,
      hasSign: true,
      scrollTop: 0,
      TabCur: 0,
      scrollLeft: 0,
      day: 0,
      score: 0,
      tabs: ['数码', '生活'],
      products: { 'digital': [], 'live': [] },

    }
  },
  filters: {
    scoreFilter (score) {
      return score / 100
    }
  },
  computed: {
    ...mapGetters('user', ['userInfo', 'isAuth', 'isIpx'])
  },
  async onLoad (option) {
    this.loading = true
    await this.getProducts()
    if (this.isAuth) {
      await this.getScore()
      await this.getSignInfo()
    }
    setTimeout(() => {
      this.loading = false
    }, 1000)
  },
  methods: {
    // 分享
    onShareAppMessage: function (e) {
      var self = this;
      return {
        title: 'RUC社团说---快来找找你的社团啦',
        imageUrl:
          'https://cloud-minapp-31286.cloud.ifanrusercontent.com/1iayhHZQUc1pbeTN.png',
        path: '/pages/finder/index'
      }
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }
          data = data[key2];
        })
      });
    },
    tabSelect (e) {
      this.TabCur = e.currentTarget.dataset.id;
      this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
    },
    // 获取签到信息
    async getSignInfo () {
      return cloudFunction('getOwnSign', {
        userId: this.userInfo.userId
      })
        .then(res => {
          const [err, data] = res
          if (data.code === 0) {
            this.hasSign = data.data.hasSign
            this.day = data.data.day
          }
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // 获取商品列表
    getProducts () {
      return cloudFunction('getProductList', {
        pageSize: 10,
        currentPage: 1
      }).then(res => {
        console.log('res', res)
        const [err, data] = res
        if (data.code === 0) {
          data.data.map(item => {
            this.products[item.cate].push(item)
          })
        }
      })
        .catch(err => {
          console.log('err', err)
        })
    },
    // 处理签到
    async handleSign (e) {
      const formId = e.detail.formId
      this.signLoading = true
      if (this.isAuth) {
        const res = await cloudFunction('handleSign', { formId })
        const [err, data] = res
        if (data.code === 0) {
          uni.showToast({ title: '签到成功!', duration: 2000 })
        }
        await this.getSignInfo()
        await this.getScore()
      } else {
        uni.showModal({
          title: '提示',
          content: 'RUC社团说需要获取QQ授权!',
          success (res) {
            if (res.confirm) {
              qq.reLaunch({ url: `/pages/user/login` })
            } else if (res.cancel) {
              console.log('用户点击取消')
            }
          }
        })
      }
      this.signLoading = false
    },
    // 获取积分信息
    async getScore () {
      return cloudFunction('getScore', {
        userId: this.userInfo.userId
      }).then(res => {
        const [err, data] = res
        if (data.code === 0) {
          this.score = data.data.score
        }
      })
    }
  },
  //页面滚动执行方式
  onPageScroll (e) {
    this.scrollTop = e.scrollTop
  }
}
